.headerLink{
    text-decoration: none;
    color: #000;
}
.navLink{
    display: block;
    text-decoration: none;
    color: #7D859D;
    padding: 10px;
    margin: 15px;
}
.active{
    display: block;
    text-decoration: none;
    color: #fff;
    background: linear-gradient(180deg, #679CF6 0%, #4072EE 100%);
    border-radius: 10px;
    padding: 10px;
    margin: 15px;
}

@media (max-width: 768px) {
    .active{
        margin: 0;

    }
    .navLink{
        margin: 0;
    }
}



